@use '../../styles/mixins';
@import '../../styles/variables';

.skills {
  height: 100%;
  padding: 20px 0;
  &__hard-list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    gap: 60px;
    height: 400px;
    align-content: center;
    padding: 0 300px;
    margin-bottom: 50px;
    background: $color-light;
  }
  &__item {
    display: inline-block;
    text-align: center;
  }
  &__img-wrapper {
    width: 90px;
    height: 90px;
    margin: 0 auto 10px;
    background-color: #fff;
    border-radius: 8px;
    overflow: hidden;
    box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.5);
    transition: transform 0.3s;
    &:hover {
      transform: scale(1.1);
    }
  }
  &__img {
    height: 100%;
    width: auto;
  }
  &__name {
    font-size: 18px;
    font-weight: 600;
    color: $color-primary;
  }
  &__soft-list {
    display: flex;
    gap: 30px;
    flex-wrap: wrap;
    justify-content: center;
    list-style: none;
    padding: 0 260px;
  }
  &__soft-list li {
    background: darken($color: $color-secondary, $amount: 10);
    border-radius: 6px;
    padding: 6px 10px;
    color: #fff;
    user-select: none;
    transition: all 0.3s;
    &:hover {
      background: $color-primary;
    }
  }
}
