@use '../../styles/mixins';
@import '../../styles/variables';

.form {
  width: 400px;
  height: 500px;
  margin: 0 auto;
  &__input {
    width: 100%;
    height: 40px;
    padding: 5px 10px;
  }
  &__textarea {
    min-width: 100%;
    height: 200px;
    resize: none;
    padding: 10px;
  }
  &__submit-btn_wrapper {
    height: 36px;
    margin: 0 auto;
    margin-top: 40px;
  }
  &__submit-btn {
    width: 100%;
    height: 100%;
    background: lighten($color: $color-primary, $amount: 16);
    border: none;
    border-radius: 6px;
    @include mixins.font-main(400, 16px, 20px);
    color: #fff;
    text-transform: uppercase;
    letter-spacing: 2px;
    cursor: pointer;
    transition: all 0.3s;
    &:hover {
      background: $color-primary;
    }
    &:active {
      transform: scaleX(96%);
    }
  }
  &__error {
    height: 10px;
    color: red;
    text-align: center;
    @include mixins.font-main(400, 14px, 18px);
    padding: 2px 0;
    margin-bottom: 30px;
  }
  &__label {
    transform: translateY(-50%);
    background: #fff;
    padding: 0 8px;
    position: absolute;
    margin-left: 8px;
    color: $color-primary;
  }
}